import React, { useState, useEffect } from 'react'
import clsx from 'clsx'

import Notifications from '../../controller/Notifications'

import { Alert, AlertTitle } from '@material-ui/lab'
import Snackbar from '@material-ui/core/Snackbar'
import Typography from '@material-ui/core/Typography'

import Dialog from '../../components/Dialog'
import Icon from '../../components/Icon'
import Inputs from '../../components/Inputs'
import LinearProgress from '../../components/Progress'


/*
 * STYLES
 */
import { emphasize, makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => {
	const emphasis = theme.palette.type === 'light' ? 0.8 : 0.98;
	const backgroundColor = emphasize(theme.palette.background.default, emphasis);
	
	return {
		AlertNone: styles => ({
			color: styles.color || theme.palette.getContrastText(backgroundColor),
			backgroundColor: styles.backgroundColor || backgroundColor,
		}),
		LinearProgressDialog: {
			margin: theme.spacing(2, -7, -2, -3)
		},
		LinearProgressSnackbar: {
			marginTop: -4
		},
		NotificationSnackbar: {
			display: 'inherit'
		},
		NotificationPopup: styles => ({
			backgroundColor: styles.backgroundColor,
			color: styles.color
		})
	}
});


export default function NotificationEvent(props) {
	const { onJump } = props;
	const [notification,setNotification] = useState(null);
	const onClose = () => setNotification(null);
	
	useEffect(() => {
		const NotificationsClass = new Notifications().getInstance();
		NotificationsClass.on('new', item => {
			
			// mark as read
			if (!item.state || item.state === 'unread') {
				NotificationsClass.set(item.id, { 'state': 'read' });
			}
			
			// change view
			if (item && item.focusTab) {
				onJump(null, item.focusTab);
			}
			
			// apply callback
			if (item && item.callback) {
				item.callback();
			}
			
			//
			setNotification(item);
		});
		
	}, [onJump]);
	
	
	// show Snackbar or Popup
	if (notification && (!notification.display || notification.display === 'modal' || notification.display === 'popup')) {
		return <NotificationPopup onClose={onClose} {...notification} />
	}
	else if (notification && (notification.display === 'snackbar' || notification.display === 'alert')) {
		return <NotificationSnackbar onClose={onClose} {...notification} />
	}
	
	return null;
}

export function NotificationPopup(props) {
	const { onClose, id, icon = null, title, message, criticality = "none", autoHideDuration = null } = props;
	const classes = useStyles({ 'color': props.fontColor, 'backgroundColor': props.backgroundColor });
	
	return (
		<Dialog
			classes={{ CustomDialogContent: classes.NotificationPopup }}
			key={'notification-' + id}
			id='NotificationPopup'
			
			open={true}
			onClose={onClose}
			
			header={message && (
				<>
					{icon && <Icon icon={icon} color={props.iconColor} style={{ marginRight: 8 }} />}
					<Typography className={classes.DialogTitleTypography} display="inline" variant="h6">
						{title}
					</Typography>
					
					{autoHideDuration !== null && (
					<LinearProgress
						className={classes.LinearProgressDialog}
						total={autoHideDuration}
						onCompleted={onClose}
						/>)}
				</>
			)}
			actions={
				<Inputs.Button key='action-close' variant="text" onClick={onClose} label="Close" />
			}
			>
			
			<div dangerouslySetInnerHTML={{ __html: message || title }} />
		</Dialog>
	);
}

export function NotificationSnackbar(props) {
	const { onClose, id, icon = null, title, message, criticality = "none", snackbarOrigin = {}, autoHideDuration = null } = props;
	const classes = useStyles({ 'color': props.fontColor, 'backgroundColor': props.backgroundColor });
	
	let AlertProps = {}
	const [open,] = useState(true);
	
	// custom icon
	if (icon) {
		AlertProps.icon = <Icon icon={icon} color={props.iconColor} />
	}
	
	return (
		<Snackbar
			classes={{ root: classes.NotificationSnackbar }}
			key={'notification-' + id}
			open={open !== false}
			anchorOrigin={{ vertical: snackbarOrigin.vertical || 'bottom', horizontal: snackbarOrigin.horizontal || 'center' }}
			//autoHideDuration={autoHideDuration}
			onClose={onClose}
			>
			
			<>
				<Alert
					classes={{ root: clsx(['error', 'warning', 'info', 'success'].indexOf(criticality) === -1 && classes.AlertNone) }}
					elevation={6}
					variant="filled"
					onClose={onClose}
					severity={['error', 'warning', 'info', 'success'].indexOf(criticality) === -1 ? 'info' : criticality}
					{...AlertProps}
					>
					
					<AlertTitle>{title}</AlertTitle>
					{message}
				</Alert>
				{autoHideDuration !== null && (
				<LinearProgress
					className={classes.LinearProgressSnackbar}
					total={autoHideDuration}
					onCompleted={onClose}
					/>)}
			</>
			
		</Snackbar>
	);
}