import React, { useRef, useState, useEffect } from 'react'
import clsx from 'clsx'


import MuiLinearProgress from '@material-ui/core/LinearProgress'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	LinearProgress: {
	},
}));


export default function LinearProgress(props) {
	const { className, onCompleted, offset = 0, total } = props;
	const classes = useStyles();
	
	const timer = useRef(null);
	const [percentage, setPercentage] = useState(offset);
	
	useEffect(() => {
		const progress = () => {
			setPercentage(prevPercentage => (++prevPercentage));
			timer.current = setTimeout(() => progress(), total / 100);
		}
		
		progress();
		return () => timer.current && clearTimeout(timer.current);
		
	}, [total]);
	
	if (percentage > 100) {
		timer.current && clearTimeout(timer.current);
		onCompleted && setTimeout(() => onCompleted(), 500);
	}
	
	return (
		<MuiLinearProgress
			classes={{ root: clsx(classes.LinearProgress, className) }}
			variant="determinate"
			value={percentage > 100 ? 100 : percentage}
			/>
	);
}